@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&@400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
*, ::before, ::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  grid-gap: 10px;
  gap: 10px;
  background-color: #eee;
  text-align: justify;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    color: white;
    background-image: url(/static/media/galaxy.f4f5a44e.jpg);
    background-position: center;
    box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
}

 header h1, h2 {
    font-size: xx-large;
    margin-left: 30px;
}

img {
    margin: 1.2rem;
    height: 200px;
    width: 200px;
    border-radius: 10%;
}
.container-main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Card {
    box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
    border-radius: 5px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    margin: 15px 0 5px 0;
    padding: 1.2rem;
    grid-gap: 25px;
    gap: 25px;
    width: 85%;
}

.item1 {
    display: grid;
    grid-template-columns: 2fr 1px 1fr;
}

.Contact {
    display: grid;
    grid-template-rows: 0.6fr 2px repeat(4, 0.4fr) 1px 0.2fr;

    justify-content: stretch;
    align-items: center;
}

.Contact h1 {
    font-size: 1.7rem;
}

.Contact a {
    /* text-decoration: none; */
    color: #000;
    font-weight: bold;
}

.Presentation {
    display: grid;
    grid-template-rows: 1fr 2px 3fr 2px 1fr;
    font-size: 1.2rem;
    text-align: justify;
}

.Presentation > * {
    display: flex;
    justify-content: center;
    align-items: center;
}
.Presentation h1{
    font-size: 2rem;
}

.CvDownload {
    text-decoration: none;
    color: black;
    background-color: greenyellow;
    padding: 10px;
    font-size: 1.2rem;
    font-weight: bold;
    text-align: center;
    margin: 15px 0 15px 0;
}

.CvDownload:active {
    transform: scale(1.05);
}

.social {
    display: flex;
    justify-content: space-around;
}

.logo {
    height: 35px;
    width: 35px;
}

.Competences {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    grid-template-rows: 1fr 2px 1fr 2px 1fr 2px;

    font-size: 1.3rem;

    justify-content: center;
    align-items: center;
    justify-items: center;
}


.Competences h1 {
    grid-column: 1 / -1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3.5rem;
    grid-gap: 50px;
    gap: 50px;
}


.Competences hr {
    grid-column: 1 / -1;
    width: 100%;
    padding: 0;
    margin: 0;
}

.Competences h3 {
    grid-column: 1 / 2;

    font-size: 2.5rem;

    display: flex;
    justify-content: center;
    align-items: center;
}

.BarProgress {
    width: 100%;
    height: 30px;
    background-color: #ddd;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
}

.progress-done {
    
    background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(217,26,26,0.7847514005602241) 0%, rgba(0,212,255,1) 100%);

    text-align: center;

    transform-origin: left;
    animation: ProgressBar .3s linear;

    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;

    width: 80%;
}

.competences img {
    grid-column: 3 / -1;
    width: 150px;
    height: 150px;
    border-radius: 0;
}

.Rotate {
    animation:spin 8s linear infinite;
}
@keyframes spin { 
    100% { 
        transform:rotate(360deg); 
    } 
}

@keyframes ProgressBar {
    0% {
        transform: scaleX(0);
        opacity: 1;
    }
}
