.container-main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Card {
    box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
    border-radius: 5px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    margin: 15px 0 5px 0;
    padding: 1.2rem;
    gap: 25px;
    width: 85%;
}

.item1 {
    display: grid;
    grid-template-columns: 2fr 1px 1fr;
}

.Contact {
    display: grid;
    grid-template-rows: 0.6fr 2px repeat(4, 0.4fr) 1px 0.2fr;

    justify-content: stretch;
    align-items: center;
}

.Contact h1 {
    font-size: 1.7rem;
}

.Contact a {
    /* text-decoration: none; */
    color: #000;
    font-weight: bold;
}

.Presentation {
    display: grid;
    grid-template-rows: 1fr 2px 3fr 2px 1fr;
    font-size: 1.2rem;
    text-align: justify;
}

.Presentation > * {
    display: flex;
    justify-content: center;
    align-items: center;
}
.Presentation h1{
    font-size: 2rem;
}

.CvDownload {
    text-decoration: none;
    color: black;
    background-color: greenyellow;
    padding: 10px;
    font-size: 1.2rem;
    font-weight: bold;
    text-align: center;
    margin: 15px 0 15px 0;
}

.CvDownload:active {
    transform: scale(1.05);
}

.social {
    display: flex;
    justify-content: space-around;
}

.logo {
    height: 35px;
    width: 35px;
}
