.Competences {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    grid-template-rows: 1fr 2px 1fr 2px 1fr 2px;

    font-size: 1.3rem;

    justify-content: center;
    align-items: center;
    justify-items: center;
}


.Competences h1 {
    grid-column: 1 / -1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3.5rem;
    gap: 50px;
}


.Competences hr {
    grid-column: 1 / -1;
    width: 100%;
    padding: 0;
    margin: 0;
}

.Competences h3 {
    grid-column: 1 / 2;

    font-size: 2.5rem;

    display: flex;
    justify-content: center;
    align-items: center;
}

.BarProgress {
    width: 100%;
    height: 30px;
    background-color: #ddd;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
}

.progress-done {
    
    background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(217,26,26,0.7847514005602241) 0%, rgba(0,212,255,1) 100%);

    text-align: center;

    transform-origin: left;
    animation: ProgressBar .3s linear;

    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;

    width: 80%;
}

.competences img {
    grid-column: 3 / -1;
    width: 150px;
    height: 150px;
    border-radius: 0;
}

.Rotate {
    -webkit-animation:spin 4s linear infinite;
    -moz-animation:spin 4s linear infinite;
    animation:spin 8s linear infinite;
}
@-moz-keyframes spin { 
    100% { -moz-transform: rotate(360deg); } 
}
@-webkit-keyframes spin { 
    100% { -webkit-transform: rotate(360deg); } 
}
@keyframes spin { 
    100% { 
        -webkit-transform: rotate(360deg); 
        transform:rotate(360deg); 
    } 
}

@keyframes ProgressBar {
    0% {
        transform: scaleX(0);
        opacity: 1;
    }
}